* {
    -webkit-tap-highlight-color: transparent;
}

body {
    margin: 0;
    padding: 0;
    background-color: #eaeaea;
    overflow: hidden;
    height: 100vh;
    width: 100vw;
}
.Abacus {
    border: solid 30px #996624;
    height: calc(75px * 8);
    width: calc(33.75px * 13);
    box-shadow: 0 0 20px rgba(0, 0, 0, 0.5);
    position: absolute;
    z-index: 10;
    cursor: -webkit-grab;
    cursor: grab;
}

.Abacus:active {
    cursor: -webkit-grabbing;
    cursor: grabbing;
}

.single-line .Abacus {
    border: solid 15px #996624;
    /* border-top: none; */
    width: calc(100vw - 15px * 2);
    -webkit-transform: translate(0, 0) !important;
            transform: translate(0, 0) !important;
}
.Row {
    height: calc(100% / 8);
    width: 100%;
    position: relative;
    cursor: auto;
    cursor: initial;
}

.Rail {
    height: 7.5px;
    background-color: #bababa;
    position: relative;
    top: calc(50% - 7.5px / 2);
}

.Die {
    height: 90%;
    width: 30px;
    position: absolute;
    top: calc(7.5px/2);
    margin: 0 1.875px;
    border-radius: 50%;
    box-shadow: 0 0 5px rgba(0, 0, 0, 0.05);
    cursor: pointer;
}

.Die.dark {
    background-color: #242014;
}

.Die.light {
    background-color: #ddcfaa;
}

.single-line .Die {
    width: 25px;
}
.AddFab {
    position: fixed;
    bottom: 40px;
    right: 40px;
    background-color: hsl(140, 72%, 62%);
    box-shadow: 0 0 13px rgba(0, 0, 0, 0.15);
    width: 80px;
    height: 80px;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
}

.AddFab img {
    pointer-events: none;
}

.AddFab:active {
    box-shadow: inset 0 0 13px rgba(0, 0, 0, 0.04);
}

.AddFab:active img {
    width: 48px;
    height: 48px;
}
.ClearFab {
    position: fixed;
    top: 40px;
    right: 40px;
    background-color: hsl(10, 72%, 62%);
    box-shadow: 0 0 13px rgba(0, 0, 0, 0.15);
    width: 80px;
    height: 80px;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
}

.ClearFab img {
    pointer-events: none;
    -webkit-transform: rotate(45deg);
            transform: rotate(45deg)
}

.ClearFab:active {
    box-shadow: inset 0 0 13px rgba(0, 0, 0, 0.04);
}

.ClearFab:active img {
    width: 48px;
    height: 48px;
}
